html, body {
  font-family: "Profession", sans-serif;
  width: 100%;
  height: auto;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}

.button {
  text-align: center;
}

.card {
  border: 0;
}

.card-header {
  font-size: 25px;
  text-decoration: underline;
  font-family: "Profession", sans-serif;
}

.card-text {
  font-size: 15px;
}

.cardblock a {
  text-align: center;
}

.card-block {
  text-align: center;
}

.card-title {
  text-align: center;
}

.card-container {
    display: grid;
    perspective: 700px;
    padding-left: 2%;
    padding-right: 2%;
}

.card-flip {
    display: grid;
    grid-template: 1fr / 1fr;
    grid-template-areas: "frontAndBack"; /*RESEARCH THIS*/
    transform-style: preserve-3d;
    transition: all 0.5s ease;
}

.front {
    grid-area: frontAndBack;
    background-color: white;
    /* background: linear-gradient(#fff, #E66465, #fff); */
}

.back {
  color: white;
  background-color: black;
  grid-area: frontAndBack;
  transform: rotateY(180deg);
  font-family: "D-DIN", sans-serif;
}

.card-container:hover .card-flip {
    transform: rotateY(-180deg);
}
